@font-face {
    font-family: HeronSansCond;
    src: url('../../assets/fonts/HeronSansCond/HeronSansCond\ Regular.otf'),
        url('../../assets/fonts/HeronSansCond/HeronSansCond\ Italic.otf');
}

@font-face {
    font-family: HeronSansCondMedium;
    src: url('../../assets/fonts/HeronSansCond/HeronSansCond\ Medium.otf'),
        url('../../assets/fonts/HeronSansCond/HeronSansCond\ Medium\ Italic.otf');
}

@font-face {
    font-family: HeronSansCondBold;
    src: url('../../assets/fonts/HeronSansCond/HeronSansCond\ Bold.otf'),
        url('../../assets/fonts/HeronSansCond/HeronSansCond\ Bold\ Italic.otf');
}

@font-face {
    font-family: HeronSansCondSemiBold;
    src: url('../../assets/fonts/HeronSansCond/HeronSansCond\ SemiBold.otf'),
        url('../../assets/fonts/HeronSansCond/HeronSansCond\ SemiBold\ Italic.otf');
}

@font-face {
    font-family: HeronSansCondLight;
    src: url('../../assets/fonts/HeronSansCond/HeronSansCond\ Light.otf'),
        url('../../assets/fonts/HeronSansCond/HeronSansCond\ Light\ Italic.otf');
}


//COMPACT DISPLAY

@font-face {
    font-family: CompactDisplayMedium;
    src: url('../../assets/fonts/CompactDisplay/sf-compact-display-medium.woff');
}

@font-face {
    font-family: CompactDisplayThin;
    src: url('../../assets/fonts/CompactDisplay/sf-compact-display-thin.woff');
}

.blog-content-screen-container {
    background-color: white;
    width: fit-content;
    height: fit-content;
    min-height: 100vh;
    min-width: 100vw;

    .article-embed-youtube-video {
        height: 480px !important;
        width: 100%;
        margin-top: 15px;

        @media screen and (min-width:1200px) and (max-width:1400px) {
            height: 420px !important;
        }

        @media screen and (min-width:992px) {
            height: 348px;
        }

        @media screen and (min-width:768px) {
            height: 400px;
        }

        @media screen and (max-width:768px) {
            height: 300px;

        }
    }

    .blog-content-screen-container {
        .cover-container {
            padding: 0px !important;
            height: 300px;
            display: flex;

            .black-overlay {
                width: 100%;
                position: absolute;
                background: rgba(0, 0, 0, 0.65);
                height: 300px;
                z-index: 99;
            }

            img {
                width: 100%;
                object-position: center;
                height: 300px;
                object-fit: cover;
                align-items: center;
                z-index: 98;

            }

            h1.article-title {
                display: flex;
                text-align: center;
                font-family: inherit;
                font-size: 35px;
                font-weight: bold;
                justify-content: center;
                align-items: center;
                position: absolute;
                width: 100%;
                height: 300px;
                z-index: 100;
                color: white;
            }
        }

        .post-and-side-container-fluid {
            background-color: white;
            min-height: 100vh;
            width: 100vw;

            .post-and-side-container {
                height: 100%;
                padding: 0px !important;

                .post-and-side-row {

                    .post-container {
                        border-right: 1.5px solid lightgray;
                        background-color: white;
                        padding: 45px 30px 30px 15px !important;

                        @media screen and(max-width: 1200px) {
                            border-right-width: 0px;
                        }

                        .image-container {
                            width: 100%;
                            margin-top: 15px;
                            border: 3px solid black;

                            img {
                                width: 100%;
                                object-fit: cover;
                            }
                        }

                        .category-container {
                            color: rgb(112, 112, 112);
                            height: 40px;
                            display: flex;
                            align-items: flex-end;
                            letter-spacing: 0.5px;
                            font: normal 16px/20px HeronSansCondMedium;
                        }

                        .title-container {
                            letter-spacing: 0.3px;
                            font: 700 42px/54px HeronSansCondBold;
                            margin-bottom: 10px;
                        }

                        .subtitle-container {
                            font: 500 30px/36px HeronSansCondMedium;
                            color: #585757;
                            margin-bottom: 15px;
                        }

                        .date-container {
                            font: 300 20px/24px HeronSansCond;
                            color: #9b9b9b;
                            margin-bottom: 15px;

                        }

                        article {
                            font: 400 21px/29px CompactDisplayMedium;
                            color: rgb(0, 0, 0);

                            a {
                                color: rgb(68, 68, 68);
                                font-weight: bold;
                            }

                            figure {
                                &:first-of-type {
                                    display: none;
                                }

                                img {
                                    width: 100%;
                                    object-fit: contain;
                                    display: block;
                                }
                            }

                            h1 {
                                font: 500 40px/45px HeronSansCondMedium;
                                color: #585757;

                                &:first-child {
                                    display: none;
                                }
                            }

                            h2 {
                                font: 500 35px/40px HeronSansCondMedium;
                                color: #585757;

                                &:first-of-type {
                                    display: none;
                                }
                            }

                            h3 {
                                font: 500 30px/35px HeronSansCondMedium;
                                color: #585757;

                                &:first-of-type {
                                    display: none;
                                }
                            }

                            p {
                                color: gray;
                                font: inherit;
                            }

                            strong {
                                color: black;
                                font: inherit;
                                font-weight: bold;
                            }

                            pre {
                                overflow: scroll;
                                background-color: black;
                                height: fit-content;
                                max-width: 100%;
                                color: white;
                                -ms-overflow-style: none;
                                /* IE and Edge */
                                scrollbar-width: none;

                                /* Firefox */
                                &::-webkit-scrollbar {
                                    display: none;
                                    /* for Chrome, Safari, and Opera */
                                }
                            }
                        }
                    }

                    .side-container {
                        min-height: 100vh;
                        background-color: white;
                        padding: 15px 30px 15px 30px !important;

                        .post-card {
                            margin-top: 45px;

                            width: calc(100%);
                            min-height: 375px;
                            box-shadow: 0px 2px 7px 0px rgba(0, 0, 0, 0.19);
                            -webkit-box-shadow: 0px 2px 7px 0px rgba(0, 0, 0, 0.19);
                            -moz-box-shadow: 0px 2px 7px 0px rgba(0, 0, 0, 0.19);

                            .image-container {
                                width: 100%;
                                border-bottom: 15px solid red;
                            }

                            .title-container {
                                height: 300px;
                                height: 150px;
                                text-align: center;

                                h4 {
                                    height: 100%;
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    color: black;
                                    font: 500 21px/32px HeronSansCond;
                                }
                            }

                        }
                    }
                }
            }
        }
    }
}